.contact-main {
  margin-top: 100px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.container__contact {
  max-width: 1300px;
  width: 100%;
  display: flex;
  gap: 50px;
  justify-content: space-evenly;
}
@media (max-width: 1024px) {
  .container__contact {
    flex-direction: column;
    justify-content: center;
  }
}
.container__contact-item {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.container__contact-item h2 {
  font-family: "Inter";
  font-weight: 500;
  font-size: 90px;
  line-height: 132.9%;
  text-transform: uppercase;
  color: #F5F5F5;
}
@media (max-width: 768px) {
  .container__contact-item h2 {
    font-size: 45px;
  }
}

.contact-item-link {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contact-item-link a {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 139.4%;
  color: #F5F5F5;
  display: flex;
  align-items: center;
  gap: 20px;
}

.img-con {
  max-width: 500px;
  width: 100%;
}